.Button { background: #8A2BE2 !important }

.Persik {
  display: block;
  width: 100%;
  max-width: 300px;
  margin: 100px auto;
}

.RichCell {
border-radius: 10px;
box-shadow: 0 0 0 2px #eee, 0 0 1px 2px #333;
margin: 10px 10px;
}
.myClass>.Panel__in>.Group {
background:  linear-gradient(270deg, #e108f7, #7a7cdf, #ff3077);
    background-size: 600% 600%;
    color: #fff !important;
    animation: AnimationName 10s ease infinite;
}
@keyframes AnimationName { 
    0%{background-position:70% 0%}
    50%{background-position:31% 100%}
    100%{background-position:70% 0%}
}

.myClass>.Panel__in>.Group>.Header>.Header__in>.Header__content { color: #fff }
